@import url("https://fonts.googleapis.com/css2?family=Heebo&family=Lato:ital@0;1&family=Poppins:wght@400;500&display=swap");
* {
  font-family: poppins;
}
*::selection {
  background: #8f9be3;
}

body {
  background: #292f40;
  margin: 0;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

.createBtn {
  transition: 0.3s all;
  border-radius: 20px;
  color: white;
  background: #03a688;
  border: none;
  padding: 0.6em 1.2em;
  width: 150px;
  font-size: 1.5em;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s all;
  margin-top: 30px;
}
.createBtn:hover {
  text-shadow: 0px 2px 9px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 20px -1px #04cea9;
}
.createBtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.createBtn:disabled:hover {
  text-shadow: none;
  box-shadow: none;
}

