}

textarea,
input[type=text] {
  font-size: 30px;
  width: 80%;
  transition: 0.3s all;
  border: 5px solid #f2ae2e;
  border-radius: 25px;
  padding: 16px;
  outline: none;
  color: #364573;
  font-weight: bold;
  background: #ffffff;
}
textarea::placeholder,
input[type=text]::placeholder {
  color: #364573;
  opacity: 0.7;
}
textarea:focus,
textarea :focus-visible,
input[type=text]:focus,
input[type=text] :focus-visible {
  box-shadow: 0px 0px 20px 1px #f8d38e;
}

.TextLength {
  pointer-events: none;
  border-radius: 10px;
  margin-top: -62px;
  color: white;
  background: rgba(102, 119, 217, 0.7);
  backdrop-filter: blur(8px);
  box-shadow: 0px 0px 20px 2px #8794e1;
  text-shadow: 0px 2px 9px rgba(0, 0, 0, 0.25);
  padding: 8px;
}

textarea {
  resize: none;
  min-height: 41vh;
}

.footer {
  border-radius: 20px 20px 0 0;
  background: #364573;
  padding: 10px 0;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
  font-size: 18px;
  transition: visibility 0.2s, opacity 0.2s linear;
}

a:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

a {
  cursor: pointer;
  transition: 0.4s all;
  color: #3abdff;
  display: inline-block;
  position: relative;
  text-decoration: none;
}
a::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #3abdff;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
  border-radius: 100px;
}
a:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
a:hover {
  text-shadow: 0px 0px 20px #3abdff;
}

.info {
  margin-top: 10px;
  color: white;
}

br {
  content: "";
  margin: 2em;
  display: block;
  font-size: 25%;
}

